import { veriDouble } from "../../Validators/index";
import { setValidator } from "../index";
/**
 * int32装饰器
 *
 * @param {string} errMsg - 错误提示
 * @returns {(target, key) => {}}
 */
export function DecoDouble(errMsg) {
    if (errMsg === void 0) { errMsg = null; }
    return function (target, key) {
        setValidator.call(this, target, key, errMsg, veriDouble);
        return;
    };
}
