import { ERROR_TYPE, CLASS_TYPE } from "../../script/staticData";
/**
 * struct类型验证
 *
 * @param {string} key - 键值
 * @param {any} value - 值
 * @returns {IVeri}
 */
export function veriStruct(key, value) {
    if (typeof value === "object" && value !== null && !(value instanceof Array)) {
        var obj = new this[CLASS_TYPE][key]();
        var error = obj.setModel(value);
        var model = obj.getModel();
        if (Object.keys(error).length !== 0) {
            if (typeof this[key] !== 'undefined' && typeof this[key].setModel === "function") {
                this[key].setModel(value);
                this[key] = this[key].getModel();
            }
            return {
                value: false,
                key: error,
                error: ERROR_TYPE.TYPE_ERROR
            };
        }
        else {
            return {
                value: true
            };
        }
    }
    else {
        if (typeof this[key] !== 'undefined' && typeof this[key].setModel === "function") {
            this[key].setModel({});
            this[key] = this[key].getModel();
        }
        return {
            value: false,
            error: ERROR_TYPE.TYPE_ERROR
        };
    }
    // if(!(value instanceof this[CLASS_TYPE][key])){
    //   //验证对象是否正确
    //   return {
    //     value: false,
    //     error: ERROR_TYPE.TYPE_ERROR
    //   };
    // }else{
    //   return {
    //     value: true
    //   };
    // }
}
