import { D_NAME } from "../script/staticData";
/*
 * 实体类父类
 */
var Validator = /** @class */ (function () {
    function Validator() {
        this.model = {};
    }
    /**
     * 设置model，获取错误信息
     *
     * @param {object} model - 参数模型
     * @returns {any}
     */
    Validator.prototype.setModel = function (model) {
        this.errMsg = {};
        if (this.initModel) {
            var container = void 0;
            var isRight = true;
            for (var key in this.initModel) {
                if (Object.prototype.hasOwnProperty.call(this, key)) {
                    try {
                        container = this[D_NAME][key];
                        isRight = true;
                        for (var i = 0; i < container.length; i++) {
                            isRight = isRight
                                && this[D_NAME][key][i].call(this, key, model[key]);
                        }
                        if (isRight) {
                            this[key] = model[key];
                        }
                        if (typeof this[key] !== "undefined") {
                            this.model[key] = this[key];
                        }
                    }
                    catch (e) {
                        console.log(e);
                    }
                }
            }
        }
        return this.errMsg;
    };
    /**
     * 获取model
     *
     * @returns {{}}
     */
    Validator.prototype.getModel = function () {
        // let retModel = {};
        // for(let key in this.model){
        //   if(typeof this.model[key] === "object" && this.model[key] !== null){
        //     if(this.model[key] instanceof Array){
        //       retModel[key] = this.getArrayModel(this.model[key]);
        //     }else {
        //       try {
        //         retModel[key] = this.model[key].getModel();
        //       } catch (e) {
        //         console.log(e);
        //       }
        //     }
        //   }else{
        //     if(typeof this.model[key] !== "undefined") {
        //       retModel[key] = this.model[key];
        //     }
        //   }
        // }
        return this.model;
    };
    /**
     * 递归读取数组
     *
     * @param {Array} data - 要递归读取的数组
     * @returns {any[]}
     */
    Validator.prototype.getArrayModel = function (data) {
        var retData = [];
        for (var i = 0; i < data.length; i++) {
            if (typeof data[i] === "object") {
                if (data[i] instanceof Array) {
                    retData.push(this.getArrayModel(data[i]));
                }
                else {
                    retData.push(data[i].getModel());
                }
            }
            else {
                retData.push(data[i]);
            }
        }
        return retData;
    };
    return Validator;
}());
export { Validator };
